html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  padding: 1.5rem;
  line-height: 1.3;
  color: #000;
  background-color: #c3c3c3;
  display: grid;
  place-items: center;
}

.container {
  width: 100%;
  min-height: 100vh;
  max-width: 1100px;
  padding: 1.5rem;
  display: grid;
  place-items: center;
  background: #fff;
}
.container header {
  width: 90%;
  background-color: salmon;
}
.container main {
  width: 90%;
  background-color: #ddd;
}
.container footer {
  width: 100%;
  background-color: #ee4b38;
}